.spinner {
    --animation-duration: 1000ms;
    width: var(--size);
    height: var(--size);
    display: flex;
    justify-content: center;
    align-items: center;
    gap: 4px;
    z-index: 1;
    .spinner-item {
        height: 40%;
        background-color: var(--clr-spinner);
        width: calc(var(--size) / 13);
        animation: spinner5 var(--animation-duration) ease-in-out infinite;
        @keyframes spinner5 {
            25% {
                transform: scaleY(2);
            }
            50% {
                transform: scaleY(1);
            }
        }
    }
    .spinner-item:nth-child(1) {
        --clr-spinner: var(--clr1);
    }
    .spinner-item:nth-child(2) {
        --clr-spinner: var(--clr3);
        animation-delay: calc(var(--animation-duration) / 10);
    }
    .spinner-item:nth-child(3) {
        --clr-spinner: var(--clr5);
        animation-delay: calc(var(--animation-duration) / 10 * 2);
    }
    .spinner-item:nth-child(4) {
        --clr-spinner: var(--clr4);
        animation-delay: calc(var(--animation-duration) / 10 * 3);
    }
    .spinner-item {
        --clr-spinner: var(--clr2);
        animation-delay: calc(var(--animation-duration) / 10 * 4);
    }
}

.lds-roller {
    display: inline-block;
    position: relative;
    width: 80px;
    height: 80px;
  }
  .lds-roller div {
    animation: lds-roller 1.2s cubic-bezier(0.5, 0, 0.5, 1) infinite;
    transform-origin: 40px 40px;
  }
  .lds-roller div:after {
    content: " ";
    display: block;
    position: absolute;
    width: 7px;
    height: 7px;
    background-color: #529CE2;
    border-radius: 50%;
    margin: -4px 0 0 -4px;
  }
  .lds-roller div:nth-child(1) {
    animation-delay: -0.036s;
  }
  .lds-roller div:nth-child(1):after {
    top: 63px;
    left: 63px;
  }
  .lds-roller div:nth-child(2) {
    animation-delay: -0.072s;
  }
  .lds-roller div:nth-child(2):after {
    top: 68px;
    left: 56px;
  }
  .lds-roller div:nth-child(3) {
    animation-delay: -0.108s;
  }
  .lds-roller div:nth-child(3):after {
    top: 71px;
    left: 48px;
  }
  .lds-roller div:nth-child(4) {
    animation-delay: -0.144s;
  }
  .lds-roller div:nth-child(4):after {
    top: 72px;
    left: 40px;
  }
  .lds-roller div:nth-child(5) {
    animation-delay: -0.18s;
  }
  .lds-roller div:nth-child(5):after {
    top: 71px;
    left: 32px;
  }
  .lds-roller div:nth-child(6) {
    animation-delay: -0.216s;
  }
  .lds-roller div:nth-child(6):after {
    top: 68px;
    left: 24px;
  }
  .lds-roller div:nth-child(7) {
    animation-delay: -0.252s;
  }
  .lds-roller div:nth-child(7):after {
    top: 63px;
    left: 17px;
  }
  .lds-roller div:nth-child(8) {
    animation-delay: -0.288s;
  }
  .lds-roller div:nth-child(8):after {
    top: 56px;
    left: 12px;
  }
  @keyframes lds-roller {
    0% {
      transform: rotate(0deg);
    }
    100% {
      transform: rotate(360deg);
    }
  }
  